import React from 'react'
import {Navigate, Routes, Route, Outlet} from 'react-router-dom'
import {PageLink, PageTitle} from '../../../_metronic/layout/core'
import {ProjectsHeader} from './ProjectsHeader'
import {Overview} from './components/Overview'
import {IndustryAcademia} from './components/IndustryAcademia'
import {National} from './components/National'


const projectsBreadCrumbs: Array<PageLink> = [
  {
    title: 'Projects',
    path: '/projects/overview',
    isSeparator: false,
    isActive: false,
  },
  {
    title: '',
    path: '',
    isSeparator: true,
    isActive: false,
  },
]

const ProjectsPage = () => (
  <Routes>
    <Route
      element={
        <>
          <ProjectsHeader />
          <Outlet />
        </>
      }
    >
      <Route
        path='overview'
        element={
          <>
            <PageTitle breadcrumbs={projectsBreadCrumbs}>Overview</PageTitle>
            <Overview />
          </>
        }
      />
      <Route
        path='ia'
        element={
          <>
            <PageTitle breadcrumbs={projectsBreadCrumbs}>Industry-Academia</PageTitle>
            <IndustryAcademia />
          </>
        }
      />
      <Route
        path='national'
        element={
          <>
            <PageTitle breadcrumbs={projectsBreadCrumbs}>National</PageTitle>
            <National />
          </>
        }
      />
    </Route>
  </Routes>
)

export default ProjectsPage

